import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEye } from '@fortawesome/free-solid-svg-icons'
import gsap from 'gsap/all'
const Project = (props) => {

    const showBtn = (n) => {
        let btn = document.getElementById("preview" + n);
        let header = document.getElementsByClassName('project-header' + n);
        let paragraph = document.getElementsByClassName('project-p' + n);
        gsap.to(btn, {duration: 0, y: 0})
        gsap.to(header, {duration: 0, x: 0})
        gsap.to(paragraph, {duration: 0, x: 0})
        gsap.to(btn, {duration: 0.1, opacity: 1, display: 'block'})
        gsap.from(btn, {duration: 0.1, y: 100})
        gsap.from(header, {duration: 0.1, x: -100})
        gsap.to(header, {duration: 0.1, display: 'block'})
        gsap.from(paragraph, {duration: 0.1, x: -100, delay: 0.1})
        gsap.to(paragraph, {duration: 0.1, display: 'block'})
        
    }
    const hideBtn = (n) => {
        let btn = document.getElementById("preview" + n);
        let header = document.getElementsByClassName('project-header' + n);
        let paragraph = document.getElementsByClassName('project-p' + n);

        gsap.to(btn, {duration: 0.1, opacity: 0, y: -100, display: 'none'})
        gsap.to(header, {duration: 0.01,x: -100,  display: 'none'})
        gsap.to(paragraph, {duration: 0.01,x: -100, delay: 0.1 ,display: 'none'})

    }
    return(
        <div onMouseEnter={() => showBtn(props.id)} onMouseLeave={() => {hideBtn(props.id)}} className="project">
            <img className="project-img" src={props.src} alt="project"/>
            <div className="project-txt-con">
             <h2 className={'project-header' + props.id}>{props.name}</h2>
             <p className={'para project-p' + props.id}>{props.skills}</p>
            </div>
            <a id={"preview" + props.id} className="previewBtn" href={props.link}><FontAwesomeIcon icon={faEye} /> preview</a>


            <div className="overlay"></div>
        </div>

    );
}

export default Project;