import gsap from "gsap/all";
import { useEffect, useState } from "react";
import opinions  from "./opinions";

const Customer = () => {
    const[indexx, setIndex] = useState(0);
    const opLength = opinions.length - 1;
    
    setTimeout(() => {
        if(indexx === opLength) {
            setIndex(0)
        } if(indexx < opLength) {
            setIndex(indexx + 1)
        }
    }, 1500);
    
    gsap.ticker.lagSmoothing(false);
    useEffect(() => {
        gsap.from('.op', {duration: 0.2, x: 10, opacity: 0});
        gsap.to('.op', {duration:0.2, opacity: 1});
        
    }, [indexx]);
    const sliderShow = (i) => {
        let x = opinions[i];

        return(
            <div className="op">
                <h3>{x.name}</h3>
                <p>{x.massege}</p>
            </div>)
    }

    return (
        
        <div className="cus">
            <div className="cus-op">
                {sliderShow(indexx)}
            </div>
        </div>
        
    )
}
export default Customer;