import Project from "./Project";
import images from './images';
import { useRef, useEffect } from "react";
import { gsap } from "gsap/all";
const Projects = () => {
    const projectsRef = useRef(null);



    useEffect(() => {
        gsap.from('.project', {duration: 0.5, scrollTrigger: '.projects-sec', opacity: 0, stagger: 0.5})}, [projectsRef]);



    return (
        <div id="portfolio" className="projects-section">
        <div ref={projectsRef} className="projects-sec">


            {images.map(img => <Project id={img.key} key={img.key} name={img.title} skills={img.skills} src={img.src} link={img.link}/>)}     
       
        </div>
        </div>
    )
}

export default Projects;  